@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.section1 {
  margin: 200px 50px;
}

.titletext {
  font-size: 45px;
  font-weight: 900;
  color: white;
  margin: 0;
}

.smalltext {
  font-size: 20px;
  font-weight: 400;
  color: white;
  margin: 20px 0 30px 0;
}

.title-button {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
  margin: 0 10px 0 0;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #fff;
  cursor: pointer;
  box-shadow: 0 2px 8px 8px rgba(0, 0, 0, 0.1);
  transition: 0.2s;
}

.invite-button {
  background-color: #61903B;
}

.title-button:hover {
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
}

.button-container {
  display: -webkit-flex;
  display: flex;
}

html, body {
  overflow-x: hidden;
}

body {
  background-color: #1F2124;
}
* {
  font-family: 'NanumSquare';
  font-weight: 700;
  user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}

.main-icon {
  -webkit-align-items: center;
          align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  float: left;
}

.main-img {
  padding: 10px 20px;
  height: 40px;
  border-radius: 50%;
}

@media screen and (max-width: 800px) {
  .section1 {
    margin: 200px 60px;
  }
}

@media screen and (max-width: 600px) {
  .nav .text-link:not(.active) {
    display: none;
  }
  .nav .icon {
    float: right;
    display: block;
    padding: 20px;
    position: absolute;
    right: 0;
    top: 0;
  }

  .active {
    background-color: #2f3136;
    display: block;
    float: none;
    width: 100vw;
    padding: 0 0 20px 0;
    transition: all 0.1s ease-out;
  }

  .active:not(:first-child) {
    position: relative;
    padding: 20px;
  }
  
  .section1 {
    text-align: center;
  }

  .button-container {
    -webkit-justify-content: center;
            justify-content: center;
  }

}


.nav {
  background-color: #2f3136;
  display: -webkit-flex;
  display: flex;

  -webkit-justify-content: space-between;

          justify-content: space-between;

  width: 100%;
  height: 65px;

  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.icon {
  display: none;
  color: #fff;
}

.text-link {
  color: #fff;
  padding: 20px;
  display: inline-block;
  text-decoration: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.footer {
  text-align: center;
  font-size: 10px;
  color: #fff;
}
